#wrapperNavbar {

    background-color: var(--col-primary-400);
    text-transform: uppercase;
    font-size: var(--fs-400);

    font-family: var(--ff-title);
    // box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 2px 0px, rgba(0, 0, 0, 0.15) 0px 1px 3px 1px;
    border-bottom: 1px solid hsl(0 0% 100% / 0.2);

    @media (min-width:992px) {
        .nav-top {
            gap: 5rem;
        }
    }

    a {
        color: white;
        padding-bottom: 0;
        &:hover {
            color: var(--col-accent-400);
        }
    }

    // #wrapperNavbar {
    //     display: flex;
    //     padding-block: 1rem;
    //     justify-content: space-between;

    .logo {
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        display: inline-block;
        background-image: url(../img/sjc-logo--wo.svg);
        aspect-ratio: 1;
        width: 80px;
    }

    .dropdown-menu {
        a {
            text-transform: initial;
        }
    }

}

